import {connect} from "react-redux";
import Slider from "./Slider";
import {bindActionCreators, Dispatch} from "redux";
import {initEdges, setEdgesTension} from "../../store/actions";

const mapStateToProps = (state: any): { state: any } => ({
    state: {}
});

function mapDispatchToProps(dispatch: Dispatch) {
    return {
        setEdgesTension: bindActionCreators(setEdgesTension, dispatch),
        initEdges: bindActionCreators(initEdges, dispatch),
    };
}

const SliderCon = connect(mapStateToProps, mapDispatchToProps)(Slider);
export default SliderCon;