import {paperInfo} from "../model";
import {SelectedPapersAction, PapersAction} from "../actions";
import {combineReducers} from "redux";

const Papers = (state: Array<paperInfo> = [], action: PapersAction) => {
    switch (action.type) {
        case "INIT_PAPERS":
            return action.initialState;
        default:
            return state;
    }
};

const SelectedPapers = (state: Array<number> = [], action: SelectedPapersAction) => {
    switch (action.type) {
        case "SET_SELECTED_PAPERS":
            return action.newState;
        default:
            return state;
    }
};


export default combineReducers({SelectedPapers, Papers});
