import {connect} from "react-redux";
import SwitchType from "./swithType";
import {bindActionCreators, Dispatch} from "redux";
import {initEdges, switchEdgesType} from "../../store/actions";

const mapStateToProps = (state: any): { state: any } => ({
    state: state
});

function mapDispatchToProps(dispatch: Dispatch) {
    return {
        switchEdgesType: bindActionCreators(switchEdgesType, dispatch),
        initEdges: bindActionCreators(initEdges, dispatch),
    };
}

const SwitchTypeCon = connect(mapStateToProps, mapDispatchToProps)(SwitchType);
export default SwitchTypeCon;