import {connect} from "react-redux";
import Nodes from "./Nodes";
import {bindActionCreators, Dispatch} from "redux";
import {setSelectedEdges, setSelectedPapers, setSelectedNodes} from "../../store/actions";

const mapStateToProps = (state: any): { state: any } => ({
    state: state
});

function mapDispatchToProps(dispatch: Dispatch) {
    return {
        setSelectedEdges: bindActionCreators(setSelectedEdges, dispatch),
        setSelectedPapers: bindActionCreators(setSelectedPapers, dispatch),
        setSelectedNodes: bindActionCreators(setSelectedNodes, dispatch),
    };
}

const NodesCon = connect(mapStateToProps, mapDispatchToProps)(Nodes);
export default NodesCon;