import {connect} from "react-redux";
import Papers from "./Papers";
import {bindActionCreators, Dispatch} from "redux";
import {setHalfSelectedEdges, setSelectedPapers, setYears, setSelectedEdges} from "../../store/actions";

const mapStateToProps = (state: any): { state: any } => ({
    state: state.Division
});

function mapDispatchToProps(dispatch: Dispatch) {
    return {
        setYears: bindActionCreators(setYears, dispatch),
        setHalfSelectedEdges: bindActionCreators(setHalfSelectedEdges, dispatch),
        setSelectedEdges: bindActionCreators(setSelectedEdges, dispatch),
        setSelectedPapers: bindActionCreators(setSelectedPapers, dispatch),
    };
}

const PapersCon = connect(mapStateToProps, mapDispatchToProps)(Papers);
export default PapersCon;